import React from 'react';
import './index.scss';

interface TitleProps {
  width?: string;
  marginTop?: string;
  title: string;
  subTitle?: string;
  content?: string[];
  contentTextAlign?: any;
  white?: boolean;
  contentWidth?: string;
}

const Title: React.FC<TitleProps> = ({
  width = '1140px',
  marginTop,
  title,
  subTitle,
  content,
  contentWidth,
  contentTextAlign = 'left',
  white = false
}) => {
  return (
    <div className="title-wrap" style={{ width, margin: 'auto', marginTop }}>
      {subTitle && (
        <div
          style={{ color: white ? '#fff' : '#6f6f6f' }}
          className="page-content-subtitle"
        >
          {subTitle.toLocaleUpperCase()}
        </div>
      )}
      <div
        style={{
          color: white ? '#fff' : '#1e1e1e',
          marginBottom: content?.length ? '60px' : '100px'
        }}
        className="page-content-title"
      >
        {title}
      </div>
      {content && content?.length && (
        <div
          style={{
            color: white ? '#fff' : '#6f6f6f',
            textAlign: contentTextAlign,
            width: contentWidth
          }}
          className="page-content"
        >
          {content.map((d, index) => (
            <div key={index}>{d}</div>
          ))}
        </div>
      )}
    </div>
  );
};

export default Title;
