import React, { CSSProperties } from 'react';

import './index.scss';

type HighlightPointsProps = {
  title: string;
  points: string[];
  style?: CSSProperties;
};

const HighlightPoints: React.FC<HighlightPointsProps> = ({
  title,
  points,
  style
}) => {
  return (
    <div className="highlight-points" style={style ?? {}}>
      <h5 className="title">{title}</h5>
      <div className="points-container">
        {points.map((point, index) => (
          <p key={index} className="point-row">
            {point}
          </p>
        ))}
      </div>
    </div>
  );
};

export default React.memo(HighlightPoints);
